:root {
    --color-primary: #6c63ff;
    --color-success: #00bf8e;
    --color-warning: #f7c94b;
    --color-danger: #e66465;
    --color-danger-variant: rgba(247, 88, 66, 0.4); 
    --color-white: #ebfffc; 
    --color-light: rgba(255, 255, 255, 0.7); 
    --color-text: #626262;
    --color-bg: #1f2641;
    --color-bg1: #2e3267; 
    --color-bg2: #424890; 
    --color-bgcontainer: linear-gradient(180deg, #130754 0%, #e66465 100%);

    --container-width-lg: 80%;
    --container-width-md: 90%;
    --container-width-sm: 94%;

    --transition: all 400ms ease;
}

.container {
    width: 607px;
    height: 829px;
    margin: auto;
    margin-top: 0;
    border-radius: 12px;
    background-image: url('https://media.giphy.com/media/rwoIPa0mKZDd6/giphy.gif');
    background-size: cover;
    /* background: linear-gradient(#e66465, #9198e5); */
}

.container-snow {
    width: 607px;
    height: 829px;
    margin: auto;
    margin-top: 0;
    border-radius: 12px;
    background-image: url('../components/Assets/snow.jpeg');
    background-size: cover;
}

.top-bar {
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 40px;
}

.top-bar input {
    display: flex;
    width: 362px;
    height: 78px;
    background: var(--color-white);
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 40px;
    color: var(--color-bg1);
    font-size: 20px;
    font-weight: 400;
}

.search-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 78px;
    height: 78px;
    background: var(--color-white);
    border-radius: 40px;
    cursor: pointer;
    transition: var(--transition);
}

.search-icon:hover {
    background: var(--color-danger);
}

/* styling after the search section */
.weather-image {
    margin-top: 25px;
    display: flex;
    justify-content: center;
}

.weather-image img {
    height: 224px;
    width: 224px;
}

.weather-temp {
    display: flex;
    justify-content: center;
    color: var(--color-white);
    font-size: 120px;
    font-weight: 400;
}

.weather-location {
    display: flex;
    justify-content: center;
    color: var(--color-white);
    font-size: 60px;
    font-weight: 400;
}

.data-container {
    margin-top: 40px;
    color: var(--color-white);
    display: flex;
    justify-content: center;
}

.element {
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap: 12px;
}

.data {
    font-size: 34px;
    font-weight: 400;
}

.text {
    font-size: 20px;
    font-weight: 400;
}

.icon {
    margin-top: 10px;
}